* {
    margin: 0;
    padding: 0px;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

@import "~react-image-gallery/styles/css/image-gallery.css";

.nav {
    background-color: #000;
}
.nav a {
    text-align: center;
    padding: 10px 0;
    color: #FFF;
    text-decoration: none;
}

.nav a:hover {
    background-color: #666;
}
.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;

    background-color: #666;
    color: #FFF;
}

@media screen and (max-width: 480px) {
    .nav {
        display: none;
        flex-direction: column;

    }
    .nav a {
        width: 100%;
    }
    .buttonmob {
        position: absolute;
        top: 5px;
        right: 10px;
    }
    .logo {
        display: none;
        justify-content: center;
        background-color: #000;
    }
    .display {
        display: flex;
    }
    .img-logo {
        height: 50px;
    }
    .image-gallery {
        margin-top: 55px;
    }
}

@media screen and (min-width: 481px) and (max-width: 768px) {
    .nav {
        display: none;
        flex-direction: column;
    }
    .nav a {
        width: 100%;
    }
    .buttonmob {
        position: absolute;
        top: 5px;
        right: 10px;
    }
    .logo {
        display: none;
        justify-content: center;
        background-color: #000;
    }
    .display {
        display: flex;
    }
    .img-logo {
        height: 50px;
    }
    .image-gallery {
        margin-top: 55px;
    }
}

@media screen and (min-width: 769px) and (max-width: 1199px) {
    .nav {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 55px;
    }
    .nav a {
        width: 25%;
    }
    .buttonmob {
        display: none;
    }
    .logo {
        position: absolute;
        top: 5px;
        left: 10px;
    }
    .display {
        display: flex;
    }
    .img-logo {
        height: 50px;
    }
    .image-gallery {
        margin-top: 5px;
    }
}

